<template>
  <div class="home">
    <div class="heade">
      <van-icon name="arrow-left" @click="go" />
      <van-search
        v-model="value"
        shape="round"
        placeholder="请输入搜索关键词"
        input-align="center"
      />
      <van-icon name="sort" @click="qh" />
    </div>
    <div class="box">
      <div class="card" v-for="index in list" :key="index.id">
        <img class="ico" :src="index.icon" />
        <div>{{ index.title }}</div>
      </div>
    </div>
    <div class="zhuti">
      <ul class="card1">
        <li
          v-for="(item, value) in imagesNameArr"
          :key="value"
          class="cardli1"
          @click="spxq(value)"
        >
          <div class="commodity">
            <div class="imgbox">
              <!-- <van-loading v-show="!zt" color="#1989fa" size="50px" verticalA /> -->
              <img
                :src="item.img"
                alt="商品"
                :class="item.gd"
              />
            </div>
            <ul>
              <li>
                <div class="describe">wenben</div>
              </li>
              <li>
                <span class="price">￥10</span>
                <span class="rebate">送: 20</span>
              </li>
              <li>
                <span class="laber1">平台自营</span>
                <span class="laber2">包邮</span>
              </li>
            </ul>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Wntj from "@/components/Wntj";
export default {
  components: {
    Wntj,
  },
  data() {
    return {
      value: "",
      search: "",
      list: [
        { id: 1, icon: require("../../public/bj1.jpg"), title: "批发市场" },
        { id: 2, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 3, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 4, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 5, icon: require("../../public/bj1.jpg"), title: "社交" },
        { id: 6, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 7, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 8, icon: require("../../public/bj1.jpg"), title: "零售市场" },
      ],
      imagesNameArr: [],
      fl: this.$store.state.Status.code,
      kgd: "",
      zt: false,
      cs: this.$refs.img,
    };
  },
  methods: {
    go() {
      this.$router.go(-1);
    },
    qh() {
      this.$store.commit("Status/setStatus", true);
      console.log(this.$store.state.Status.code);
    },
    spxq(value) {
      this.$router.push(`/spxq/${value}`);
    },
  },
  created() {
    const cs = this.$store.state.Cs.imagenamelist;
    let gd = 0;
    let src = '';
    let classtype = '';
    for (let i = 0; i < cs.length; i++) {
      if (cs[i].slice(0, 2) == this.fl) {
        const imgA = new Image();
        imgA.src = require("../../public/ldl_tp/" + cs[i]);
        imgA.onload = (cs) => {
          gd = imgA.height;
          src = cs.target.src;
          if (gd < 900) {
            classtype = "imgstaus1";
            } else if (gd < 1200) {
              classtype = "imgstaus2";
            } else if (gd < 1500) {
              classtype = "imgstaus3";
            } else if (gd < 1700) {
              classtype = "imgstaus4";
            } else {
              classtype = "imgstaus5";
            }
          this.imagesNameArr.push({ 'img': src, 'height': gd,'gd': classtype });
          // this.imagesNameArr.sort((a, b) => {
          //   return a.height - b.height;
          // });
        };
      }
    }
     console.log(this.imagesNameArr);
  },
  mounted() {
   
  },
};
</script>

