  justify-content: flex-start;
  // column-gap: 10px;
  // column-count: 2;
  .cardli1 {
    // break-inside: avoid;
    max-width: 46vw;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: aliceblue;
