  background-color: rgba(255, 0, 0, 0.959);
  color: white;
  border: 1px solid #ccc;
  padding: 1px;
  border-radius: 5px;
}
.laber2 {
  box-sizing: border-box;
  font-size: 12px;
  color: rgba(255, 145, 0, 0.959);
  border: 2px solid rgba(255, 145, 0, 0.959);
